<template>
    <FcPage title="茶园确权" isBorder showLeft>
        <div class="teaConfirm" ref="teaConfirm">
            <div class="teaConfirm-title">
                <FCstatistics :renderList="statisticsRenderList" :data="totalData"></FCstatistics>
                <FcAgriculturalTab v-model="tabIndex" :renderList="renderList" @change="handleChangeTab" :totalData="totalData"></FcAgriculturalTab>
            </div>
            <div class="teaConfirm-content" ref="teaConfirm-content">
                <FcList :page="currPage" :limit="limit" :count="count" :loaded="loaded" @scrolltolower="handleScrolltolower">
                    <div class="teaConfirm-list">
                        <teaConfirmItem v-for="(item, key) in list" :key="key" :data="item" @delete="handleDelete(item, key, $event)" @edit="handleEdit"></teaConfirmItem>
                    </div>
                </FcList>
            </div>
            <div class="teaConfirm-apply" @click="handleClickApply">
                <img src="@/assets/enterprise/app/teaConfirmList/apply.png" alt="">
            </div>
        </div>
    </FcPage>
</template>

<script>
import { getStatisticsRenderList, getTeaConfirmTabsRenderList  } from "./config"
import FCstatistics from '@/components/statistics/statistics.vue'
import FcAgriculturalTab from '@/components/agriculturalTab/agriculturalTab.vue'
import teaConfirmItem from '@/components/teaConfirmItem/teaConfirmItem.vue'
import FcList from '@/components/list/list.vue'
import { enterpriseUserDataStorage } from '@/utils/storage'
import { isEmpty } from '@/utils/common'
export default {
    name: 'teaConfirm',
    components: {
        FcList,
        FCstatistics,
        FcAgriculturalTab,
        teaConfirmItem
    },

    data() {
        return {
            statisticsRenderList: getStatisticsRenderList(),
            renderList: getTeaConfirmTabsRenderList(),
            userData: enterpriseUserDataStorage.getUserData(),
            totalData: {},
            list: [],
            tabIndex: -1,
            limit: 15,
            page: 1,
            currPage: 0,
			count: 0,
			loaded: false
        }
    },
    created() {
        this.getTeaConfirmTotalData()
        this.getTeaConfirmList()
    },

    mounted() { },

    methods: {
        /**
            获取茶园确权统计数据
            @param 
            @return
        */
        getTeaConfirmTotalData() {
            this.$axios({
                url: '/tea/auth/listAuthorize',
				data: {
					tenantId: this.userData.tenantId
                }
            }).then((res) => {
                if (!isEmpty(res.data)) {
                    res.data.allNum = res.data.authorizedNum + res.data.rejectedNum + res.data.toApprovalNum
                    this.totalData = res.data
                }
            })
        },
        /**
            获取茶园确权列表
            @param 
            @return
        */
		getTeaConfirmList(callback) {
			this.loaded = false
			const params = {
				limit: this.limit,
                page: this.page,
                tenantName: this.userData.tenantName
			}
			if (this.tabIndex != -1) {
				params.status = this.tabIndex
			}
            this.$axios({
                url: '/tea/auth/pageAuthorize',
				data: {
					...params
                }
            }).then((res) => {
                if (Array.isArray(res.data)) {
                    if (this.page == 1) {
                        this.list = res.data
                    } else {
                        this.list = this.list.concat(res.data)
                    }
                } else {
                    if (this.page == 1) {
                        this.list = []
                    }
                }
                this.currPage = this.page
				this.loaded = true
                this.count = res.count
                callback && typeof callback === 'function' && callback()
            }).catch(() => {
                this.loaded = true
                this.count = 0
                if (this.page == 1) {
                    this.list = []
                }
                callback && typeof callback === 'function' && callback()
            })
		},

		/**
            处理点击删除时触发
            @param { Object } item 确权项
            @param { Number } key 确权项的下标
            @param { Event } objecct 子组件传递的事件参数
            @return
        */
        handleDelete(item, key, {callback}) {
            this.$axios({
                url: '/tea/auth/delAuthorize',
                method: 'GET',
                data: {
                    id: item.id
                }
            }).then((res) => {
                this.$toast("删除成功")
                this.list.splice(key, 1)
                callback()
            })
        },

        /**
            处理点击申请页面
            @param 
            @return
        */
        handleClickApply() {
            this.$router.push({ path: '/enterprise/teaConfirm/add' })
		},

		/**
            处理点击编辑事件
            @param { Object } item 工单项
            @return
        */
        handleEdit(item) {
			this.$router.push({
				path: '/enterprise/teaConfirm/add',
				query: {
					id: item.id
				}
			})
        },
        /**
            处理容器滚动事件
            @param 
            @return
        */
        handleScrolltolower(callback) {
            this.page++
            this.getTeaConfirmList(callback)
        },
        /**
            处理顶部tabs改变事件
            @param 
            @return
        */
		handleChangeTab() {
			this.page = 1
			this.getTeaConfirmList()
        }
    },

}

</script>

<style scoped lang='scss'>
.teaConfirm {
	position: relative;
    height: 100%;
    background-color: #f5f5f5;
}
.teaConfirm-title {
    padding: 0 0.3rem;
    /deep/ .agricultural-tabs {
		height: 2.24rem;
        border-top: none;
    }
}
.teaConfirm-content {
	overflow-y: auto;
	position: absolute;
	left: 0;
	right: 0;
	top: 4.6rem;
	bottom: 0;
	// padding-bottom: 1.08rem;
}
.teaConfirm-apply {
    z-index: 9;
    position: fixed;
    right: 0.3rem;
    bottom: 0.6rem;
    > img {
        width: 1.04rem;
        height: 1.04rem;
    }
}
</style>


