<template>
    <div class="teaConfirmItem">
        <SwipeCell :before-close="beforeSwipeCellClose" :name="data.id" :disabled="data.status != teaConfirmStatusDict.wait">
            <div class="teaConfirmItem-item-box">
                <FcItemTitle :title="data.teaName" :img="statusImgObj[data.status]"></FcItemTitle>
                <div class="teaConfirmItem-content">
                    <FcItemCell :text="`茶园面积：${data.teaArea}亩`" :img="require('@/components/images/teaConfirmList/area.png')"></FcItemCell>
                    <FcItemCell :text="`茶园地址：${data.teaAddress}`" :img="require('@/components/images/taeGreenItem/location.png')"></FcItemCell>
                    <FcItemCell :text="`含硒量：${data.seleniumContent}mg/kg`" :img="require('@/components/images/taeGreenItem/desc.png')"></FcItemCell>
                    <FcItemCell :text="`申请时间：${data.createTime}`" :img="require('@/components/images/taeGreenItem/complateTime.png')"></FcItemCell>
                    <FcItemCell :text="`负责人：${data.personInCharge} ${data.phone}`" :img="require('@/components/images/taeGreenItem/allotUser.png')"></FcItemCell>
                    <FcItemCell v-if="data.status == teaConfirmStatusDict.reject" :text="`驳回原因：${data.personInCharge}`" :img="require('@/components/images/taeGreenItem/workCode.png')"></FcItemCell>
                    <FcEdit v-if="data.status == teaConfirmStatusDict.wait" @edit="handleEdit(data)"></FcEdit>
                </div>
            </div>

            <template #right>
                <div class="agricultural-item-delete">
                    <img src="@/components/images/agriculturalList/delete.png" alt="">
                </div>
            </template>
        </SwipeCell>
    </div>
</template>

<script>
import { SwipeCell, Dialog } from 'vant'
import { teaConfirmStatusDict } from '@/utils/dict'
import FcItemTitle from '@/components/itemTitle/itemTitle.vue'
import FcEdit from '@/components/edit/edit.vue'
import FcEmpty from '@/components/empty/empty.vue'
import FcItemCell from '@/components/itemCell/itemCell.vue'
import waitImg from '@/components/images/teaConfirmList/wait.png'
import complateImg from '@/components/images/teaConfirmList/complate.png'
import rejectImg from '@/components/images/teaConfirmList/reject.png'
export default {
    name: 'teaConfirmList',

    components: {
        FcEmpty,
        SwipeCell,
        FcItemTitle,
        FcItemCell,
        FcEdit,
        Dialog
    },
    props: {
        data: {
            type: Object,
            default: () => {}
        }
    },
    data() {
        return {
            statusImgObj: {
                '0': waitImg,
                '1': complateImg,
                '2': rejectImg
            },
            teaConfirmStatusDict: teaConfirmStatusDict
        }
    },
    computed: {
        /**
            是否显示已加载全部
            @param 
            @return { Boolean } 是否显示已加载全部
        */
        showAll() {
            if (this.count > 0) {
                if (this.count <= this.page * this.limit) {
                    return true
                } else {
                    return false
                }
            } else {
                return false
            }
        }
    },

    mounted() { },

    methods: {
        /**
            自定义滑动单元格关闭时的行为
            @param { Object } object 事件参数对象 
            @param { Object } item 点击的工单项数据 
            @param { Number } key 点击工单项的下标 
            @return
        */
        beforeSwipeCellClose({ name, position, instance }) {
            switch (position) {
                case 'right':
                    Dialog.confirm({
                        message: '确定删除吗？',
                    }).then(() => {
                        this.$emit('delete', {
                            callback: () => { instance.close() }
                        })

                    });
                    break;
            }
        },

        /**
            处理点击编辑事件
            @param { Object } item 工单项
            @return
        */
        handleEdit(item) {
            this.$emit('edit', item)
        },
    },

}

</script>

<style scoped lang='scss'>
.teaConfirmList {
    padding: 0 0.3rem;
}
.teaConfirmItem {
    overflow: hidden;
    margin-bottom: 0.2rem;
    background-color: #fff;
    border-radius: 0.08rem;
}
.teaConfirmItem-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 1.02rem;
    padding: 0 0.3rem;
    border-bottom: 1px solid #eee;
    > div {
        font-weight: 600;
    }
    > img {
        width: 1.36rem;
        height: 0.44rem;
    }
}
.teaConfirmItem-content {
    overflow: hidden;
    padding: 0.3rem;
    .teaConfirmItem-infoItem {
        display: flex;
        flex-direction: row;
        margin-bottom: 0.2rem;
        > img {
            position: relative;
            top: 0.06rem;
            display: block;
            width: 0.3rem;
            height: 0.3rem;
        }
        > div {
            margin-left: 0.16rem;
            font-size: 0.28rem;
            line-height: 0.4rem;
            color: #151515;
        }
    }
    .teaConfirmItem-infoItem:last-child {
        margin-bottom: 0;
    }
}

.teaConfirmItem {
    .agricultural-item-delete {
        width: 1.04rem;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: #cf3723;
        > img {
            width: 0.4rem;
            height: 0.4rem;
        }
    }

}
</style>


